body{font-size: 0.24rem; max-width: 750px; min-width: 320px;  font-size: 0.6rem; padding-top: 1.4rem; color: #131313; margin: 0 auto;  background-color: #efefef; font-family: 'Helvetica Neue', Helvetica, sans-serif;}
body.indexbody{background-color: #f6f6f6;}
body.bgfff{background-color: #fff;}
body.bgchat{background-color: #f5f5f5;}
div{box-sizing:border-box; -moz-box-sizing:border-box; /* Firefox */ -webkit-box-sizing:border-box; /* Safari */}
article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary {display:block;}
ul,li{list-style:none;}
p,ul,li,ol,dl,dt,dd,img{margin: 0; padding: 0; border:none}
h1,h2,h3,h4,h5,h6{margin: 0; padding: 0; font-weight: normal; font-size: 0.7rem;}
i{font-style: normal;}
button, select, textarea{border:none; cursor: pointer;  font-family: 'Helvetica Neue', Helvetica, sans-serif, '\5FAE\8F6F\96C5\9ED1';}
button, select{color: #fff;}
button,input[type="submit"],input[type="button"]{ border:none; cursor: pointer; color: #fff; font-family: 'Helvetica Neue', Helvetica, sans-serif, '\5FAE\8F6F\96C5\9ED1';}
.clearfix:after {content:".";display:block;height:0;clear:both;visibility:hidden;}
