/*header*/
.header{width:100%; height:1.4rem; background-color: #da3838; color: #fff; position: fixed; left:0; top:0; z-index: 100;}
.header h1.header-title{width:100%; line-height: 1.4rem; font-size: .7rem;}
.footer{width:100%; position: fixed; -webkit-box-pack:center; -ms-flex-pack:center; justify-content:center; color: #fff; bottom: 0; left:0; height: 48px; background-color: #1d1919; display:-webkit-box; display:-ms-flexbox; display:flex; -webkit-display:flex;}
.footer .flink{-webkit-box-flex:1;-ms-flex:1;flex:1; display: block;}
.flink a{color: #fff; text-decoration: none;}
.flink .footico{width:16px; height: 16px; display: block; margin: 6px auto 3px; font-size: 18px;}
.flink span{font-size: .5rem;}
.footer.home .flink:nth-child(1) a{color:#da3838}
.footer.joke .flink:nth-child(2) a{color:#da3838}
.footer.life .flink:nth-child(3) a{color:#da3838}
.footer.member .flink:nth-child(4) a{color:#da3838}
/*推荐新闻*/
.rec-channe{padding: .6rem 0 .4rem; border-bottom: 0.06rem solid #e3e2e2; overflow: hidden;}
.rec-channe ul{width:1800%}
.rec-channe ul li{float: left; height: .8rem; line-height: .8rem; display: block; padding: 0 .5rem; text-align: center; margin: 0 .3rem .3rem 0; font-size: .6rem;}
.rec-channe ul li.curr{color: #da3838;}
.ind-recnews{}
.recnews-title{height: 2rem; line-height: 2rem; font-size: .8rem; color: #da3838; border-bottom: 1px solid #e7e7e7;}
.news-box{border-bottom: 1px solid #ededed;}
.news-box .news-btitle{font-size: .75rem; padding: .3rem 0; line-height: 1.2rem; max-height: 3rem;}
.news-bimg .imgso{width:33.33%; padding-right: 4px;}
.news-bimg .imgso img{height: 4rem;}
.new-other .newoth-source{color: #3d99cb;}
.new-other .newoth-time{color: #a5a5a5;}
.newsbtn{width:4rem; height: 2rem; background-color: #42B983; color: #fff; margin: 1rem;}
/*笑话推荐*/
.jokenews{}
.joke-nav{}
.joke-nav .tabtype{display:inline-block; padding: 0 .4rem; height: 1.4rem; line-height: 1.4rem; margin-right: .5rem; border-radius: .12rem; border:1px solid #da3838}
.joke-nav .tabtype.curr{color:#fff; background-color: #da3838;}
.jokenews-box{}
.jokenews-box .j-title{}
.j-title a{font-size: 1rem; color: #2a2a2a;}
.j-tytime .j-type{color:#2693ba; display: inline-block; margin:0 .5rem;}
.j-tytime .j-time{display: inline-block; color: #999;}
.j-photo{padding:.3rem 1.4rem}
.j-photo img{max-width: 100%; margin: 0 auto;}
.wlt-title{font-size: 1rem;}
.wlt-content{padding:.5rem .8rem; line-height: 1.4rem; font-size: .78rem;}
/*九宫格-ui*/
.wrap-grids{position: relative; overflow: hidden;}
.wrap-grids::after,.wrap-grids::before {content: " ";position: absolute;left: 0px;top: 0px;color: rgb(217, 217, 217);}
.wrap-grids::before {right: 0px;height: 1px;-webkit-transform-origin: 0px 0px 0px;transform-origin: 0px 0px 0px;-webkit-transform: scaleY(0.5);transform: scaleY(0.5); border-top: 1px solid rgb(217, 217, 217);}
.wrap-grids::after {width: 1px;bottom: 0px;-webkit-transform-origin: 0px 0px 0px;transform-origin: 0px 0px 0px;-webkit-transform: scaleX(0.5);transform: scaleX(0.5);border-left: 1px solid rgb(217, 217, 217);}
.wrap-grids .wgrids-box{position: relative;float: left;width: 33.3333%;-webkit-box-sizing: border-box;box-sizing: border-box;padding: 20px 10px;}
.wrap-grids .wgrids-box::before,.wrap-grids .wgrids-box::after{content: " ";position: absolute;right: 0px;bottom: 0px;color: rgb(217, 217, 217);}
.wrap-grids .wgrids-box::before{top: 0px;width: 1px;-webkit-transform-origin: 100% 0px 0px;transform-origin: 100% 0px 0px;-webkit-transform: scaleX(0.5);transform: scaleX(0.5);border-right: 1px solid rgb(217, 217, 217);}
.wrap-grids .wgrids-box::after{left: 0px;height: 1px;-webkit-transform-origin: 0px 100% 0px;transform-origin: 0px 100% 0px;-webkit-transform: scaleY(0.5);transform: scaleY(0.5);border-bottom: 1px solid rgb(217, 217, 217);}
.wgrids-box .wgrids-ico{width:28px; height: 28px; margin: 0 auto;}
.wgrids-ico i{font-size: 1.3rem;}
.wgrids-box .wgrids-title{margin-top: 5px; font-size: .7rem;}
/*生活*/
.life-detail .ldet-title{font-size: .9rem;}
.life-detail .ldet-updatatime{}
/*快递 - 搜索*/
.del-logo{width:36%; margin:0 auto; padding: 1rem 0;}
.del-logo img{width: 100%; background-color: transparent;}
.life-box{}
.life-box .life-input{width:80%; height: 2.3rem; line-height: 2.3rem; padding:0 5%; border:1px solid #3278e6; font-size: .95rem;}
.life-box .life-input::-webkit-input-placeholder{font-size: .65rem;}
.life-box .life-input:-ms-input-placeholder{font-size: .65rem;}
.life-box .life-input::-ms-input-placeholder{font-size: .65rem;}
.life-box .life-input::placeholder{font-size: .65rem;}
.life-box .life-btn{width:20%; height: 2.3rem; background-color: #3278e6; border:1px solid #3278e6}
/*login*/
.login-wrap{}
.log-logo{width:44%; margin: 4rem auto 0;}
.log-logo.reg-logo{margin: 2rem auto 0;}
.log-logo img{background-color: transparent;}
.login-wmain{margin-top: 2rem;}
.log-box{height:2rem; margin-bottom: .8rem; border-bottom:1px solid #e7e7e7; position: relative;}
.log-box .log-ico{width:18px; height: 18px; font-size: 18px; color: #da3838; display: block; position: absolute; top:.46rem; left:.4rem; }
.log-box .log-ico.log-telico{font-size: 24px; left:.6rem;}
.log-box .log-inp{width:100%; height: 100%; font-size: 16px; color: #3a3a3a; border:none; padding-left: 2rem;}
.log-box .log-inp::-webkit-input-placeholder {font-size: 14px; color: #ccc;}
.log-btn{width:100%; height: 2rem; background-color: #da3838; border-radius: 3px; font-size: .75rem;}
.log-other{height: 1rem;}
.log-other .log-tips{color: #eb1a1a;}
/*机器人聊天室*/
.chat-wrap{padding: .8rem .8rem 2.8rem .8rem;}
.bot-fix{width:100%; padding: .3rem 0; background-color: #fff; border-top:1px solid #d2d2d2; position: fixed; left:0; bottom:0}
.bot-fix .chat-inp{width:80%; height: 1.2rem; border: none; height: 100%; padding: .5rem; float: left; resize:none}
.bot-fix .chat-sendbtn{width:20%; float: right; margin-top: .2rem;}
.chat-sendbtn button{width:80%; height: 1.4rem; color: #fff; border-radius: 3px; background-color: #da3838; border: 0;}
.chat-welcome{font-size: .6rem; color: #949494;}
.chat-server,.chat-client{display: -webkit-box;display: -ms-flexbox;display: flex; -webkit-display:flex;}
.chat-stx{width:1.5rem; height: 1.5rem; border-radius: 50%; background: url(data:image/png;base64,) no-repeat; background-size: cover;}
.chat-ctx{width:1.5rem; height: 1.5rem; border-radius: 50%; background: url(data:image/png;base64,) no-repeat; background-size: cover;}
.chat-stext,.chat-ctext{-webkit-box-flex:1;-ms-flex:1;flex:1;}
.chat-stext{margin-left: .5rem;}
.chat-ctext{margin-right: .5rem;}
.stext-show{background-color: #fff; float: left;}
.ctext-show{background-color: #9eea6a; float: right;}
.stext-show,.ctext-show{width:auto; display: inline-block; font-size: .65rem; padding: .4rem .5rem; border-radius: 3px;}







